<template>
    <div class="dynamic-selected-tnved">
        <h1>{{headers[0]}}</h1>
        <highcharts class="chart" :options="topChartOptions" :deepCopyOnUpdate="true"></highcharts>
        <h1>{{headers[1]}}</h1>
        <div class="d-table">
            <div class="d-tr" v-for="(item, index) in dynamicTable" :key="index">
                <div class="d-td div-as-button" @click="itemDataRequest(item.label)">{{item.label}}</div>
                <div class="d-td">{{item.weight}}</div>
                <div class="d-td">{{item.dynamicWeight}}</div>
                <div class="d-td">{{item.stoim}}</div>
                <div class="d-td">{{item.dynamicStoim}}</div>
            </div>
        </div>
        <h1>{{headers[2]}}</h1>
        <highcharts class="chart" :options="firstPieOptions"></highcharts>
        <h1>{{headers[3]}}</h1>
        <highcharts class="chart" :options="secondPieOptions"></highcharts>

        <highcharts class="chart" :options="avgBarOptions"></highcharts>
    </div>
</template>

<script>
    import Vue from "vue";
    import HighchartsVue from "highcharts-vue";
    import Highcharts from "highcharts/highcharts";
    import dataModule from "highcharts/modules/data";
    import drilldown from "highcharts/modules/drilldown";
    import {HTTP} from '../http-common'
    import qs from 'qs'
    import moment from 'moment'

    import threeDimensionsHC from "highcharts/highcharts-3d";

    dataModule(Highcharts);
    drilldown(Highcharts);
    threeDimensionsHC(Highcharts);
    Vue.use(HighchartsVue);
    export default {
        name: "MiddleDownBlock",
        props: ['get_by', 'headers', 'block_place', 'date', 'params', 'interval', 'category', 'item_list'],
        data() {
            return {
                items_data: [],
                date_label: [],
                topChartOptions: {
                    xAxis: {
                        categories: null
                    },
                    series: [
                        {
                            name: '',
                            data: []
                        },
                        {
                            name: '',
                            data: []
                        },

                    ]
                },
                firstPieData: [],
                firstPieOptions: {
                    plotOptions: {
                        series: {
                            dataLabels: {
                                enabled: true,
                                format: '{point.name}: {point.percentage:.2f}%'
                            }
                        }
                    },
                    tooltip: {
                        pointFormat: ': <b>{point.y}</b><br/>'
                    },
                    chart: {
                        type: "pie"
                    },

                    series: [{
                        data: [],
                    }],
                },
                secondPieOptions: {
                    plotOptions: {
                        series: {
                            dataLabels: {
                                enabled: true,
                                format: '{point.name}: {point.percentage:.2f}%'
                            }
                        }
                    },
                    tooltip: {
                        // headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
                        pointFormat: ': <b>{point.y}</b><br/>'
                    },
                    chart: {
                        type: "pie"
                    },
                    series: [{
                        data: [],
                    }],
                },
                avgBarOptions: {
                    chart: {
                        type: 'column'
                    },
                    xAxis: {
                        type: 'category',
                        labels: {
                            rotation: -45,
                            style: {
                                fontSize: '13px',
                                fontFamily: 'Verdana, sans-serif'
                            }
                        }
                    },
                    legend: {
                        enabled: false
                    },
                    series: [{
                        data: [],
                    }]

                },
            }
        },
        watch: {
            firstPieData: {
                handler(val) {
                    let sorted_arr = val.slice().sort((a, b) => {
                        if (this.category === '') {
                            a = (this.params === 'stoim') ? a.imp.cost : a.imp.weight;
                            b = (this.params === 'stoim') ? b.imp.cost : b.imp.weight;
                        } else {
                            a = (this.params === 'stoim') ? a.exp.cost : a.exp.weight;
                            b = (this.params === 'stoim') ? b.exp.cost : b.exp.weight;
                        }
                        return b - a
                    });
                    let value = []
                    for (let i of sorted_arr.slice(0, 9)) {
                        let data
                        if (this.category === '') {
                            data = (this.params === 'stoim') ? i.imp.cost : i.imp.weight
                        } else {
                            data = (this.params === 'netto') ? i.exp.cost : i.exp.weight
                        }
                        value.push({name: i.item, y: data});
                    }
                    let sum_data = 0
                    for (let i of sorted_arr.slice(10)) {
                        let data
                        if (this.category === '') {
                            data = (this.params === 'stoim') ? i.imp.cost : i.imp.weight
                        } else {
                            data = (this.params === 'netto') ? i.exp.cost : i.exp.weight
                        }
                        sum_data += data
                    }
                    value.push({name: '', y: sum_data})
                    this.firstPieOptions.series[0].data = value
                },
                deep: true,
            }
        },
        computed: {
            dynamicTable: function () {
                let value = [];
                let weight_arr = [];
                let stoim_arr = [];

                for (let data of this.items_data) {
                    let item = (this.category === '') ? data.imp : data.exp
                    let weight = item.weight
                    let stoim = item.cost

                    weight_arr.push(weight)
                    stoim_arr.push(stoim)

                    value.push(
                        {
                            short_label: data.item_short,
                            label: data.item,
                            weight: weight,
                            stoim: stoim,
                        });
                }
                weight_arr = this.dynamics_array(weight_arr);
                stoim_arr = this.dynamics_array(stoim_arr);
                for (let i = 0; i < value.length; i++) {
                    value[i].dynamicWeight = weight_arr[i];
                    value[i].dynamicStoim = stoim_arr[i];
                }
                return value
            },
        },
        methods: {
            dynamics_array(arr) {
                let new_arr = [0];
                for (let i = 0; i < arr.length; i++) {
                    let val = (arr[i] === 0) ? 0 : ((arr[i+1] - arr[i])/arr[i] * 100).toFixed(2);
                    new_arr.push(val)
                }
                return new_arr
            },
            updateSecondPie(val) {
                this.secondPieOptions.series[0].data = [];
                for (let el of val) {
                    let data;
                    if (this.category === '') {
                        data = (this.params === 'stoim') ? el.imp.cost : el.imp.weight
                    } else {
                        data = (this.params === 'netto') ? el.exp.cost : el.exp.weight
                    }
                    this.secondPieOptions.series[0].data.push([el.item, data])
                }
            },

            recount() {
                HTTP.get((this.block_place === 'middle') ? 'statistic/report/middle_block/' : 'statistic/report/down_block/', {
                    params: {
                        'date_to': (this.date.from != null && this.date.to != null) ? this.date.to : moment(new Date()).format('YYYY-MM'),
                        'date_from': (this.date.from != null && this.date.to != null) ? this.date.from : moment(new Date()).subtract(3, 'year').format('YYYY-MM'),
                        'interval': this.interval,
                        'item_list': this.item_list,
                        'item_list_length': this.item_list.length,
                        'get_by': this.get_by,
                    },
                    paramsSerializer: params => {
                        return qs.stringify(params)
                    }
                })
                    .then(response => {
                        this.firstPieData = response.data.pie;
                        this.items_data = response.data.table;
                    })
            },
            itemDataRequest(item) {
                HTTP.get((this.block_place === 'middle') ? 'statistic/report/middle_block/detailed/' : 'statistic/report/down_block/detailed/', {
                    params: {
                        'date_to': (this.date.from != null && this.date.to != null) ? this.date.to : moment(new Date()).format('YYYY-MM'),
                        'date_from': (this.date.from != null && this.date.to != null) ? this.date.from : moment(new Date()).subtract(3, 'year').format('YYYY-MM'),
                        'interval': this.interval,
                        'item_list': this.item_list,
                        'item_list_length': this.item_list.length,
                        'item': item,
                        'get_by': this.get_by
                    },
                    paramsSerializer: params => {
                        return qs.stringify(params)
                    }
                })
                    .then(response => {
                        this.topChartOptions.series[0].data = (this.params === 'stoim') ? response.data.data.imp.cost : response.data.data.imp.weight;
                        this.topChartOptions.series[1].data = (this.params === 'stoim') ? response.data.data.exp.cost : response.data.data.exp.weight;
                        this.topChartOptions.xAxis.categories = response.data.labels;
                        this.date_labels = response.data.labels;
                        this.updateAvgChart(response.data.data);
                        this.updateSecondPie(response.data.extend_data)
                    })
            },
            updateAvgChart(val) {
                this.avgBarOptions.series[0].data = [];
                for (let i = 0; i < this.date_labels.length; i++) {
                    let data;
                    if (this.category === '') {
                        data = (this.params === 'stoim') ? val.imp.cost[i] : val.imp.weight[i]
                    } else {
                        data = (this.params === 'netto') ? val.exp.cost[i] : val.exp.weight[i]
                    }
                    this.avgBarOptions.series[0].data.push([this.date_labels[i], data])
                }
            },
        },
        created() {
            this.$eventHub.$on('recount', this.recount)
        },
        beforeDestroy(){
            this.$eventHub.$off('recount');
        },
    }
</script>

